* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f0f0f0;
  font-family: Arial, sans-serif;
}

p {
  padding-top: 20px;
}

.container {
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.card {
  max-width: 400px;
  background-color: #fff;
  border-radius: 4px;
  margin: 20px;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.title {
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: bold;
}

.breed-select {
  width: 30%;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 16px;
}

.loader, .error {
  text-align: center;
  margin-bottom: 20px;
  font-size: 18px;
  display: none;
}

.loader:after {
  content: "";
  width: 18px;
  height: 18px;
  border: 2px solid rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0) #007bff;
  border-radius: 50%;
  animation: spin .8s linear infinite;
  display: inline-block;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

